@import url(https://fonts.googleapis.com/css2?family=Arimo:ital,wght@0,400;0,700;1,400;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Cart{position:fixed;top:0;right:0;bottom:0;left:0;display:flex;flex-direction:column;align-items:flex-end;padding:0;background-color:rgba(0,0,0,.5);color:#111;z-index:101;transition:ease .3s}.CartContainer{display:flex;flex-direction:column;padding:24px 32px;width:100%;max-width:600px;height:100%;background-color:#fff;transition:width ease 300ms}.CartContainer .CartContent{color:#111;transition:ease 300ms}.CartContainer .CartContent h2{font-size:2rem}.CartContainer .CartContent ul{list-style:none;margin-left:0;padding-left:0;font-size:1.6rem}.CartContainer .CartContent ul li{margin-bottom:.8rem}.CartContainer .CartContent h3{font-size:1.6rem}.title{display:flex;justify-content:space-between;align-items:center}.cart-icon{position:relative}.cart-icon .cart-number{position:absolute;display:flex;justify-content:center;align-items:center;top:-4px;right:-4px;width:16px;height:16px;border-radius:8px;font-size:1rem;background:#0cc276;color:#fff}.cart-content{margin-top:48px}.line-cart{display:flex;justify-content:space-between;font-size:1.4rem;margin-bottom:8px}.line-cart .price{font-weight:bold}.line-total{display:flex;justify-content:space-between;font-size:1.6rem;margin-top:16px}.line-total .price{font-weight:bold}.counter{display:flex;align-items:center;border-radius:15px}.counter p{margin:0 8px}
.Header{position:fixed;top:0;left:0;right:0;display:flex;align-items:center;justify-content:space-between;padding:0 32px;height:80px;background:#fff;z-index:100;border-bottom:1px solid #111}.Header .logo{display:flex;align-items:center}.Header h1{font-size:1.8rem}.Header .App-logo{width:48px;height:48px;margin-right:.8rem;animation:spin infinite 20s linear}@keyframes spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@media(max-width: 768px){.Header{padding:0 16px}}
.ShoppingContainer{display:flex;flex-direction:column;flex-wrap:wrap;margin-top:80px;padding:0;width:100%}.ShoppingContainer form{max-width:100%;padding:1.6rem;display:flex;justify-content:center}.ShoppingContainer form .radio{display:flex;padding-left:16px;padding-right:16px}.ShoppingContainer form .radio label{display:flex;align-items:center;font-size:1.6rem;height:32px;font-weight:bold;padding-left:1.6rem;padding-right:1.6rem;background:#fff;color:#111;border-radius:1.6rem;cursor:pointer;align-self:flex-start;transition:ease-in .3s}.ShoppingContainer form .radio label:hover{background:#f5f5f5}.ShoppingContainer form .radio input{display:none}.ShoppingContainer form .radio input:checked+label{background:#111;color:#fff}.ShoppingList{list-style:none;display:flex;margin:0;padding:0;flex-wrap:wrap;width:100%;border-top:1px solid #111}@media(max-width: 768px){.ShoppingContainer form{overflow-x:scroll;padding:1.6rem 0;justify-content:flex-start}.ShoppingContainer form::-webkit-scrollbar{display:none}.ShoppingContainer form .radio{white-space:nowrap}}
.ProductTile{position:relative;cursor:pointer;display:flex;flex-direction:column;width:calc(100/3 * 1%);padding:0;border:1px solid #111;border-left:none;border-top:none}.ProductTile:nth-of-type(3n){border-right:none}.ProductTile .cover{object-fit:cover;object-position:center center;height:33vw}.ProductTile .InformationContainer{display:flex;flex-wrap:wrap;justify-content:space-between;align-items:center;margin-bottom:1.6rem;padding:.8rem 1rem}.ProductTile .TextContainer{display:flex;flex-wrap:wrap;padding:.8rem 0 .8rem 1.6rem}.ProductTile h2{font-size:1.6rem;font-weight:normal;margin-right:1.6rem}.ProductTile h3{font-size:1.6rem;position:relative;margin-left:3.2rem;margin-right:.8rem}.ProductTile h3::before{content:"";position:absolute;background:#c8c8c8;margin-top:1.2rem;margin-left:-3.2rem;width:1.6rem;height:.1rem}.ProductTile .tag{position:absolute;display:flex;align-items:center;top:1.6rem;height:2.4rem;padding:0 .8rem;right:1.6rem;color:#111;background:#fff;border:1px solid #111;font-size:1.4rem;font-weight:bold;margin-bottom:.8rem;border-radius:1.2rem;display:flex;align-self:flex-start;justify-self:flex-end}.ProductTile .care{font-size:1.3rem}.ProductTile .Button{font-size:1.4rem;font-weight:bold;background:#fff;align-self:flex-end;margin:.4rem 0 .4rem auto;border:none;color:#111;padding-left:1.6rem;padding-right:1.6rem;height:32px;border-radius:20px;cursor:pointer;transition:ease-in .3s}.ProductTile .Button:hover{background:#111;color:#fff}@media(max-width: 768px){.ProductTile{width:calc(100/2 * 1%)}.ProductTile:nth-of-type(2n){border-right:none}.ProductTile:nth-of-type(3n){border-right:1px solid #111}.ProductTile .cover{object-fit:cover;object-position:center center;height:60vw}.ProductTile .InformationContainer{flex-direction:column}.ProductTile .InformationContainer .TextContainer{display:flex;flex-direction:column;width:100%;padding:.8rem 0}.ProductTile h3{margin-left:0;margin-right:0}.ProductTile h3::before{display:none}.ProductTile .Button{margin-top:1.6rem;align-self:flex-end;border:1px solid #111}}
.recomandation{position:fixed;z-index:101;bottom:16px;background:#fff;display:flex;padding:16px;max-width:360px;border:1px solid #111;overflow:hidden;transition:ease-in .3s}.recomandation ::before{content:"";width:4px;height:100%;background:#0cc276;position:absolute;top:0;left:0}.recomandation p{font-size:1.6rem;margin-right:16px}
.recomandation{position:fixed;z-index:101;bottom:16px;background:#0cc276;display:flex;padding:16px;max-width:360px;border:1px solid #111;overflow:hidden;transition:ease-in .3s}.recomandation ::before{content:"";width:4px;height:100%;background:#0cc276;position:absolute;top:0;left:0}.recomandation p{font-size:1.6rem;margin-right:16px}
.footer{display:flex;flex-direction:column;width:100%;margin-top:auto;justify-content:center;align-items:center;height:300px;background:#fff;border-top:1px solid #111;margin-top:-1px}.footer .footerTitle{font-size:1.8rem;margin-bottom:3.2rem}.footer .formContainer{min-width:343px}.footer .error{color:red;font-size:1.4rem;margin-left:2.4rem;margin-top:.8rem}.footer form{display:flex;justify-content:flex-start;align-items:center;font-size:1.6rem;background:#fff;color:#111;padding-left:.8rem;padding-right:.8rem;height:48px;border:1px solid #111;border-radius:24px}.footer input{display:flex;background:none;width:100%;padding-left:24px;padding-right:24px;font-size:1.6rem;border:none;outline-offset:none;outline-style:none}.footer button{border:none;font-size:1.4rem;font-weight:bold;background:#fff;color:#111;padding-left:1.6rem;padding-right:1.6rem;height:3.2rem;border-radius:20px;cursor:pointer;transition:ease-in .3s}.footer button:hover{background:#111;color:#fff}
*,::after,::before{box-sizing:border-box}#root{display:flex;flex-direction:column;min-height:100vh}body,html{font-family:"Arimo","Helvetica Neue",Helvetica,Arial,sans-serif;font-size:10px;line-height:1.6;background-color:#fff;color:#111;-webkit-font-smoothing:antialiased}.App{display:flex;flex-direction:column;align-items:center;justify-content:flex-start;min-height:100vh}h1,h2,h3,h4,h5,h6,p,a{margin:0;line-height:150%}.icon{width:24px;height:24px;cursor:pointer}.icon .dark{color:#111}.mini-icon{width:16px;height:16px;border:1px solid #111;border-radius:.8rem;cursor:pointer}.mini-icon .dark{color:#111}
